<template>
  <div>
    <v-card-title class="text-h5" style="color: #273746"> </v-card-title>
    <v-card elevation="0" outlined>
      <v-card-title>
        <div>
          {{ nom }}
        </div>
      </v-card-title>
      <v-card-text>
        <strong>
          <div>
            {{ description }}
          </div>
        </strong>
      </v-card-text>
      <v-divider class="mx-4"> </v-divider>

      <v-card-text>
        <!-- 
          Se ha ocultado temporalmente..
          <v-text-field
          class="text-capitalize"
          outlined
          v-model="date"
          label="Fecha"
          required
        ></v-text-field> -->
        <v-chip-group v-model="value" active-class="primary white--text" column>
          <v-chip value="yes"> cumple </v-chip>
          <v-chip value="no"> no cumple </v-chip>
        </v-chip-group>
      </v-card-text>
    </v-card>
  </div>
</template>
<script>
export default {
  name: "ItemBasicDate",
  props: {
    template: Object,
  },
  data() {
    return {
      description: null,
      nom: null,
      value: null,
      date: null,
    };
  },
  watch: {
    value(e) {
      this.$emit("setOption", e);
    },
    date(e) {
      this.$emit("setDate", e);
    },
    template() {
      this.start();
    },
  },
  created() {
    this.start();
  },
  methods: {
    start() {
      this.description = this.template.description;
      this.nom = this.template.nom ? this.template.nom : this.template.text;
      this.value = this.template.value;
      this.date = this.template.date;
    },
    /*  change() {
      Soon
      this.$emit("setOption", this.value);
      Father
      @setOption="(value) => (item.value = value)"
    }, */
  },
};
</script>